___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --bg-color: #f5f5dc;
  /* --primary: #086788;
  --secondary: #17b3d6; */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-style: inherit;
}

body {
  font-family: sans-serif;
  background-color: var(--bg-color);
}

button {
  padding: 8px 12px;
  border: 1px solid black;
  border-radius: 4px;
  background-color: var(--bg-color);
  font-weight: bold;
  cursor: pointer;
}
button:hover {
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.5);
}

a {
  color: inherit;
  text-decoration: none;
}

body > header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 12px 0;
  max-width: 750px;
  margin: 0 auto;
}

header > p {
  text-align: center;
}

header > span {
  margin: 12px 0;
}

main {
  display: flex;
  justify-content: center;
  max-width: 750px;
  gap: 12px;
  margin: auto;
  margin-bottom: 16px;
}

main > .image-input-box {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 400px;
  border-radius: 8px;
  border: 2px dotted black;
  background-position: center;
  background-size: cover;
}

main > .image-input-box > .file {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

main > .image-input-box > canvas {
  width: 95%;
  height: 95%;
}

main > .customization-form {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

#color_wrapper {
  background-color: black;
  display: inline-block;
  visibility: hidden;
}

#color_wrapper::before {
  content: "";
  position: absolute;
  border-radius: 3px;
  outline: black solid 2px;
  border: white solid 3px;
  height: 2rem;
  width: 2rem;
  pointer-events: none;
  background-color: inherit;
  visibility: visible;
  box-sizing: border-box;
}

#background-color {
  opacity: 0;
  height: 2rem;
  width: 2rem;
  box-sizing: border-box;
  pointer-events: initial;
  visibility: visible;
}

#background-alpha {
  filter: grayscale(100%);
  visibility: visible;
}

.customization-form > .action-buttons-container {
  display: flex;
  gap: 8px;
}

.customization-form > .action-buttons-container > button {
  flex: 1;
}
